<template>
  <v-container>
    <v-btn color="cred" class="me-1" @click="goBack">
      <v-icon small>mdi-chevron-left</v-icon>
      Back
    </v-btn>
    <v-btn color="primary">
      <v-icon>mdi-heart-outline</v-icon>
      Add Yo Wishlist
    </v-btn>
    <v-row>
      <div class="mobile">
        <v-col cols="12" md="6" sm="12">
          <div class="ratio ratio-16x9">
              <v-img v-if="videoDetail.youtube_id==null" :src="('https://placehold.co/600x400?text='+videoDetail.video_title)+' No video'" class="embed-responsive-item"></v-img>
              <iframe v-if="videoDetail.youtube_id!=null" :src="('https://www.youtube.com/embed/'+videoDetail.youtube_id)" class="embed-responsive-item"></iframe>
          </div>
        </v-col>
        <v-col cols="12" md="6" sm="12">
          <div class="small d-flex">
            <div class="p-0 mdetails">
              <h6>{{ videoDetail.video_title }}</h6>
              <h6>By: <router-link to="/businessprofile" class="text-decoration-none">{{ videoDetail.name }}</router-link></h6>
            </div>
            <div class="ratings align-center d-flex mb-3">
              <v-rating :value=videoDetail.rating_avrage color="amber" dense half-increments size="20" readonly></v-rating>
              <div class="white--text badge bg-dark ms-2">{{ videoDetail.total_reviews }}</div>
            </div>
            <div class="mcontact">
                <a :href="('tel:'+videoDetail.mobile_phone)" class="text-decoration-none m-1">
                    <v-btn fab small color="success" class="mb-2">
                      <v-icon>mdi-phone</v-icon>
                      <!-- {{ videoDetail.mobile_phone }} -->
                    </v-btn>
                </a>
                <a :href="('tel:'+videoDetail.mobile_phone)" class="text-decoration-none">
                  <v-btn fab small color="primary">
                    <v-icon>mdi-whatsapp</v-icon>
                    <!-- {{ videoDetail.mobile_phone }} -->
                  </v-btn>
                </a>
            </div>
          </div>
          <v-expansion-panels class="mt-4" focusable v-model="panel1">
            <v-expansion-panel v-for="(item, i) in 1" :key="i">
              <v-expansion-panel-header class="fw-bold">Details</v-expansion-panel-header>
              <v-expansion-panel-content class="">
                <div class="mb-3">
                  <v-card-text>
                    <p class="py-2 text-justify">{{ videoDetail.video_description }}</p>
                  </v-card-text>
                </div>
              </v-expansion-panel-content>
            </v-expansion-panel>
          </v-expansion-panels>
        </v-col>
      </div>
      <div class="desktop">
        <v-row>
          <v-col cols="12" md="6">
            <div class="ratio ratio-16x9">
              <v-img v-if="videoDetail.youtube_id==null" :src="('https://placehold.co/600x400?text='+videoDetail.video_title)+' No video'" cover class="embed-responsive-item"></v-img>
              <iframe v-if="videoDetail.youtube_id!=null" :src="('https://www.youtube.com/embed/'+videoDetail.youtube_id)" class="embed-responsive-item"></iframe>
            </div>
          </v-col>
          <v-col cols="12" md="6">
            <v-card-text class="p-0">
              <h5>{{ videoDetail.video_title }}</h5>
              <h6>By: <router-link :to="{name:'businessprofile',params:{id:videoDetail.user_id, uname:videoDetail.name}}" class="text-decoration-none">{{ videoDetail.name }}</router-link></h6>
              <div class="ratings align-center d-flex mb-3">
                <v-rating :value=videoDetail.rating_avrage color="amber" dense half-increments size="20" readonly></v-rating>
                <div class="white--text badge bg-dark ms-2">{{ videoDetail.total_reviews }}</div>
              </div>
              <div class="mb-3">
                <v-icon small>mdi-map-marker</v-icon>
                  <div v-if="videoDetail.address!=null">
                    {{ videoDetail.address }}, 
                  </div>
                  {{ videoDetail.city }}, {{ videoDetail.state }}
              </div>
              <div>
                <a :href="('tel:'+videoDetail.mobile_phone)" class="text-decoration-none m-1">
                  <v-btn class="cred">
                    <v-icon small>mdi-phone</v-icon>{{ videoDetail.mobile_phone }}</v-btn>
                </a>
                <a :href="('tel:'+videoDetail.mobile_phone)" class="text-decoration-none">
                  <v-btn color="success">
                    <v-icon small>mdi-whatsapp</v-icon>{{ videoDetail.mobile_phone }}
                  </v-btn>
                </a>
              </div>
              <p class="py-2 text-justify">{{ videoDetail.video_description }}</p>
            </v-card-text>
          </v-col>
        </v-row>
      </div>
    </v-row>
    <v-row class="mt-10">
      <v-col cols="12" md="12">
        <div class="mobile" v-if="nearByHotDealsVideos.length > 1">
          <div class="ms-2 mb-1"><span class="bbred cblu fw-bold">You May Visit</span></div>
          <carousel :autoplay="false" :margin="30" :dots="false" :navText="['‹', '›']" :slideBy="1"
                :responsive = "{ 0: { items: 1 }, 576: { items: 2 }, 768: { items: 3 }, 1200: { items: 4 } }" 
                class="vsearch mobile">
            <div v-for="(jewel, i) in nearByHotDealsVideos" :key="i" class="item mitem">
              <div @click="handleClick(nearVideo.video_id)">
                <router-link :to="{name:'videodetail',params:{video_id:jewel.video_id}}" class="small d-flex link-dark">
                  <div class="mimg">
                    <v-img v-if="jewel.youtube_id==null" :src="('https://placehold.co/600x400?text='+jewel.video_title)+' No video'" width="120" class="rounded-3" ></v-img>
                    <iframe v-if="jewel.youtube_id!=null" :src="('https://www.youtube.com/embed/'+jewel.youtube_id)" width="120" height="100" class="rounded-3" ></iframe>
                  </div>
                  <div class="mdetails">
                    <h5 @click="handleClick(nearVideo.video_id)">
                      <router-link :to="{name:'videodetail',params:{video_id:jewel.video_id}}" class="mtitle link-dark">
                        {{ jewel.video_title }}
                      </router-link>
                    </h5>
                    <div>Add to Wishlist<v-icon small>mdi-heart</v-icon></div>
                    <p class="mb-0">{{ jewel.validity }}</p>
                    <v-icon small>mdi-map-marker</v-icon>
                    <div v-if="jewel.address!=null">
                        {{ jewel.address }}, 
                    </div>
                      {{ jewel.city }},{{ jewel.state }}
                  </div>
                </router-link>
              </div>
            </div>
          </carousel>
        </div>
        <div v-else class="mobile">
            <div class="h5 mb-4"><span class="bbred cblu fw-bold">You May Visit</span></div>
            <h6>No Related Videos.</h6>
        </div>
        <div class="desktop" v-if="nearByHotDealsVideos.length > 1">
          <div class="h5 mb-4"><span class="bbred cblu fw-bold">You May Visit</span></div>
          <carousel :autoplay="false" :margin="30" :dots="false"
                    :navText="['‹', '›']" :slideBy="1"
                    :responsive = "{ 0: { items: 1 }, 576: { items: 2 }, 768: { items: 3 }, 1200: { items: 4 } }"
                    class="vsearch">
            <div v-for="(nearVideo, i) in nearByHotDealsVideos" :key="i" class="item">
              <div @click="handleClick(nearVideo.video_id)">
                <router-link :to="{name:'videodetail',params:{video_id:nearVideo.video_id}}" class="link-dark text-decoration-none">
                  <v-card>
                    <v-img v-if="nearVideo.youtube_id==null" :src="('https://placehold.co/600x400?text='+nearVideo.video_title)+' No video'" cover height="150"></v-img>
                    <iframe v-if="nearVideo.youtube_id!=null" :src="('https://www.youtube.com/embed/'+nearVideo.youtube_id)" cover height="150"></iframe>
  
                    <v-card-text class="h160">
                      <h5 @click="handleClick(nearVideo.video_id)">
                        <router-link :to="{name:'videodetail',params:{video_id:nearVideo.video_id}}" class="link-dark text-decoration-none">
                          {{ nearVideo.video_title }}
                        </router-link>
                      </h5>
                      <div>Add to Wishlist<v-icon small>mdi-heart</v-icon></div>

                      <div class="ratings align-center d-flex">
                        <v-rating :value=nearVideo.rating_avrage color="amber" dense half-increments size="20" readonly></v-rating>
                        <div class="white--text badge bg-dark ms-2">{{ nearVideo.total_reviews }}</div>
                      </div>

                          <v-icon small>mdi-map-marker</v-icon>
                          <div v-if="nearVideo.address!=null">
                              {{ nearVideo.address }}, 
                          </div> {{ nearVideo.city }}, {{ nearVideo.state }}

                    </v-card-text>
                  </v-card>
                </router-link>
              </div>
            </div>
          </carousel>
        </div>
        <div v-else class="desktop">
            <div class="h5 mb-4"><span class="bbred cblu fw-bold">You May Visit</span></div>
            <h6>No Related Videos.</h6>
        </div>

      </v-col>
      <v-col>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import axios from "axios";
import carousel from "vue-owl-carousel";

export default {
    name: 'VideoDetail',
    props: {
      video_id: Number
    },
    components: { carousel },
    
    data() {
        return {
            videoDetail: [],
            nearByHotDealsVideos: [],
        }
    },
    mounted() {
      this.businessprofile();
      // this.addToCount();
    },

  methods:{
    goBack() {
      window.history.back();
    },
    businessprofile(){
      axios.get(`/video/detail/${this.video_id}`)
          .then((resp) =>{
            this.videoDetail = resp.data.videoProfile;
            this.nearByHotDealsVideos = resp.data.nearByHotDealsVideos;
        })
    },
    handleClick(video_id) {
      this.videoDetail = [];
      this.nearByHotDealsVideos = [];
      axios.get(`/video/detail/${video_id}`)
          .then((resp) =>{
            this.videoDetail = resp.data.videoProfile;
            this.nearByHotDealsVideos = resp.data.nearByHotDealsVideos;
        })
    }
  },

}
</script>
<style scoped>span.border-s {
    border-bottom: 1px solid red;
    color: #0165a3;
}

.h160 {
    height: 160px;
    overflow: hidden;
}
.item.mitem {
  box-shadow: 0 6px 10px -6px rgba(0,0,0,0.5);
  background: #f1f2f380;
  padding: 7px;
}
.mimg {
  width: 110px;
}
.mtitle {
  font-weight: 600;
  font-size: 12.25px;
}
.mdetails {
  width: 100%;
  padding: 0px 0 0 25px;
}
.mcontact {
  width: 42px;
}
.fs11{font-size: 10.4px;}
</style>